//The purpose of creating this GLobalState is to avoid prop drilling conecept, without having some contextAPI or Redux we will have to pass functions all the way down to whatever file we need via all. It could be useless when it comes to a big file structure. So as a solution I use contextAPI here.

import React, { createContext, useReducer } from "react";
import AppReducer from "./AppReducer";

//Initial state
const initialState = {
  transactions: [],
};

// Create context
export const GlobalContext = createContext(initialState); // creating context and exporting using createContext hook and passing my initialState object which contains transactions[].

// Provider component
export const GlobalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(AppReducer, initialState);

  //Actions
  function deleteTransaction(id) {
    dispatch({
      type: "DELETE_TRANSACTION",
      payload: id,
    });
  }

  function addTransaction(transaction) {
    dispatch({
      type: "ADD_TRANSACTION",
      payload: transaction,
    });
  }

  return (
    <GlobalContext.Provider
      value={{
        transactions: state.transactions,
        deleteTransaction,
        addTransaction,
      }}
    >
      {children}
    </GlobalContext.Provider>
  );
};
